import Vue from "vue";
import getUserLocale from 'get-user-locale';
import { BootstrapVue, IconsPlugin } from 'bootstrap-vue'
import AsyncComputed from 'vue-async-computed'
import VueFlags from "@growthbunker/vueflags";

import browserDetect from "vue-browser-detect-plugin";

// import SBStore from "./SBStore"
import App from "./App.vue";
import router from "./router";
import { library } from '@fortawesome/fontawesome-svg-core'
import { faUserSecret } from '@fortawesome/free-solid-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/vue-fontawesome'

// import './registerServiceWorker';
// import toaster from "./toaster";
// import moment from "./moment";
import 'bootstrap-vue/dist/bootstrap-vue.css'
import Argon from "./plugins/argon/argon-kit";
import i18n from './i18n'
const VueHead = require('vue-head')
async function init() {
  const toaster = await (await import(/* webpackChunkName: "toaster" */ '@/plugins/sb/toaster')).default;
  const moment = await (await import(/* webpackChunkName: "moment" */ '@/plugins/sb/moment')).default;

  Vue.use(VueHead, {
    separator: '-',
    complement: 'Ֆ osu.ppy.sb'
  })
  
  // Vue.use(SBStore);
  Vue.use(VueFlags);
  Vue.use(BootstrapVue)
  Vue.use(IconsPlugin)
  Vue.use(AsyncComputed)
  Vue.use(Argon);
  Vue.use(toaster);
  Vue.use(moment);
  Vue.use(browserDetect);

  library.add(faUserSecret)

  Vue.component('font-awesome-icon', FontAwesomeIcon)


  // Vue.config.productionTip = false;

  const locale = getUserLocale() || 'en-gb'
  new Vue({
    router,
    render: h => h(App),
    i18n,

    // store
    data: {
      // locale: 'en-gb',
      silentTranslationWarn: process.env.NODE_ENV === 'production',
      locale,
      host: 'https://osu.ppy.sb',
      rippleApiBase: 'https://osu.ppy.sb/api/v1',
      peppyApiBase: 'https://osu.ppy.sb/api'
    }
  }).$mount("#app");
  // console.log(getUserLocale())
}
init();