<template>
    <base-dropdown tag="li" class="nav-item">
        <a
            slot="title"
            href="#"
            class="nav-link"
            data-toggle="dropdown"
            role="button"
            @click.prevent
        >
            <i class="ni ni-collection d-lg-none" />
            <span class="nav-link-inner--text">{{$t("locale")}}(Set Locale)</span>
        </a>
        <a class="dropdown-item" @click.prevent="switchLocale('zh-CN')">
            <gb-flag code="CN" size="small" icon-path="assets/flags" class="mr-1" />中文(中国)
        </a>
        <a class="dropdown-item" @click.prevent="switchLocale('en-GB')">
            <gb-flag code="GB" size="small" icon-path="assets/flags" class="mr-1" />English(International)
        </a>
        <a class="dropdown-item" @click.prevent="switchLocale('en-US')">
            <gb-flag code="US" size="small" icon-path="assets/flags" class="mr-1" />English(US)
        </a>
    </base-dropdown>
</template>
<script>
import BaseDropdown from "@/components/argon-components/BaseDropdown";
export default {
    components: {
        BaseDropdown
    },
    created() {
        this.setLocale();
    },
    methods: {
        switchLocale(locale) {
            this.log(locale, "locale");
            this.$root.locale = locale;
            this.setLocale();
        },
        setLocale() {
            this.$root.$i18n.locale = this.$root.locale;
        }
    }
};
</script>